nav {
    display: flex;
    justify-content: center;
    background-color: rgba(15, 15, 15, 0.4);
    position: -webkit-sticky;
    position: sticky;
    z-index: 100;
    top: -1px;
    transition: 350ms;
}

nav:hover {
    background-color: rgba(15, 15, 15, 0.8);
}

.nav-item {
    text-decoration: none;
    margin: 15px;
    font-family: Sans-serif;
    transition: 400ms;
    color: #bbbbbb;
    padding: 5px 10px 5px 10px;
    border-radius: 5px
}

.nav-item:hover {
    color: #ffffff;
}

.header {
    font-size: 70px;
    font-family: Trebuchet MS;
    text-align: center;
}

body {
    margin: 0%;
    background: url("https://www.timeforkids.com/wp-content/uploads/2019/09/final-cover-forest.jpg?w=1024");
    background-attachment: fixed;
    overflow-x: hidden;
    background-position-x: center;
    background-size: 100%;
    font-family: Quicksand;
  }
  

p, h1, h2, h3 {
    color: white
}

p {
    font-family: Quicksand;
    font-size: 20px;
}

.content {
    background-color: #4d4d4d;
    margin-bottom: 0px;
    padding: 15px;
}
.abt-card-cont {
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin: -15px;
}

.ab-sect-card {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.gray {
    background-color: rgb(120, 120, 120);
}

.interest {
    font-size: 66px;
    width: 500px;
    text-align: center;
}

.interest, .ab-sect-cont {
    border-style: solid;
    padding: 40px 0;
    
}

.ab-sect-cont {
    width: 890px;
}
